import React, { Component } from 'react'
import ReactDOM from 'react-dom'


//Components
import JSON from './db.json'
import Header from './components/header'
import NewsList from './components/news_list'

class App extends Component {

    state = {
        news: JSON,
        filtered: []
    }

    getKeyword = (e) => {
        let keyword = e.target.value
        let filtered = this.state.news.filter((item) => {
            return item.title.indexOf(keyword) > -1 // -- if the e.target.value (input text form header) is true (> -1 or is more than -1 which of course js starts at 0 not 
                                                    // 1 so that is to say that something is there)
        })
        this.setState({
            filtered
        })
        // console.log(filtered)
        // console.log(e.target.value) //this takes the event data that is entered from the input field in the header and passes
        //                             // it back to this file
    }

    render() {
        console.log(this.state.news)
        let newsFiltered = this.state.filtered
        let newsWhole = this.state.news
        return (
            <div>
                <Header keywords={this.getKeyword}/>
                <NewsList news={newsFiltered.length === 0 ? newsWhole : newsFiltered}/> 
                {/* This creates and object passes the data from the props in this file 
                (ie from all of the files that have been imported)
                and then sends them to the NewsList component like magic */}
            </div>
        )
    }
}  

ReactDOM.render(<App/>, document.querySelector('#root')) 
